var shifty = require("shifty");
cc.Class({
    extends: cc.Component,

    properties: {
        screenShowTime: 1,
        screenHideTime: 2
    },

    onLoad(){
        //this.screenShowTime *= 1000; //converting to ms
        this.screenShowTime *= 1000;
        this.screenHideTime *= 1000; //converting to ms
    },
    init(callback){

        var hideTime = this.screenHideTime;
        var logoNode = this.node;
        setTimeout(function(){
            logoNode.active = true;
            var tweenObj = {
                from: {opacity:255},
                to: {opacity:0},
                duration: hideTime,
                easing:'linear',
                step: function(state){
                    logoNode.opacity = state.opacity;
                }
            }
            shifty.tween(tweenObj).then(function(){
                logoNode.active = false;
                callback();
            });
        }, this.screenShowTime)

        

    },
    show(callback){
        this.node.active = true;
        callback();

        //enable node
        //enable controls
        //play showing animation
    },
    hide(callback){
        this.node.active = false;
        callback();
        //disable node
        //disable controls
        //show hiding animation
    },

});
