var shifty = require("shifty");
cc.Class({
    extends: cc.Component,

    properties: {
        verticalLength: 100,
        time: 1
    },


    onLoad () {
        this.time *= 2000;
        this.node.opacity = 0;
    },
    triggerAnimation(){
        var myNode = this.node;
        var tweenObj = {
            from: {x:this.node.x, y:this.node.y, opacity: 255},
            to: {x:this.node.x, y:this.node.y+this.verticalLength, opacity: 0},
            duration: this.time,
            easing:'easeOutQuad',
            step: function(state){
                myNode.x = state.x;
                myNode.y = state.y;
                myNode.opacity = state.opacity;
            }
        }

        shifty.tween(tweenObj).then(function(){myNode.parent.removeChild(myNode)})
    }

});
