var CONST_SCENE = require('scene');

cc.Class({
    extends: cc.Component,

    properties: {
    },

    onCollisionEnter: function(other, self){
        if(other.node.name != 'ball') return;
        if(CONST_SCENE.GAMEOVER_SCREEN_CONTROLLER.win == -1) return; //means if has been setted by finish
        CONST_SCENE.GAMEOVER_SCREEN_CONTROLLER.win = 1;
        CONST_SCENE.MANAGER_SCREEN_CONTROLLER.gameoverScreen();
    },

    
});
