var CONST_EVENT = require('event');
var CONST_SCENE = require('scene');
var shifty = require("shifty");
var partsDefination = require('partsDefination');
var that = null;
var self = null;
cc.Class({
    extends: cc.Component,
    properties: {
            parts:[cc.Prefab],
            star:cc.Prefab,
            point: cc.Prefab,
            highlighter: cc.Prefab,
            powerup_projectile: cc.Prefab, //longe projectile power ring
            powerup_magnet: cc.Prefab, //longe projectile power ring
            finishLine: cc.Prefab,
            speedBoost: cc.Prefab,
            gravityReverseBoost: cc.Prefab,
            player: cc.Prefab,
    },
    onLoad () {
        window.pg = this;
        this.partsSpawnArea = this.node.parent.getChildByName('partsSpawnArea');
        this.justStarted  = true;
        this.part_1 = null;
        this.part_2 = null;  
        this.partPreviousPoint = null;
        this.currentOutfit = null;
        this.currentBodyFrame = null;
        this.currentHandsFrame = null;
        this.highlighterNode = null;
        this.partsData = [];
        this.currentPartIndex = null;
        this.ballNode = CONST_SCENE.BALL_NODE;
        that = this;
        self = this;
        //difficulty parameters initialization
        this._partIndex = 0;
        this._xDev = 0;
        this._yDev = 0;
        this._powerup = 0;
        this._star = 0.7;
        
        var t = CONST_SCENE.CUSTOMIZATION_SCREEN_CONTROLLER.getCurrentOutfit();
        this.currentBodyFrame = t.outfitFrame;
        this.currentHandsFrame = t.handsFrame;
    

        //events
        cc.systemEvent.on(CONST_EVENT.OUTFIT_CHANGE, this.onOutfitChange, this);
        CONST_SCENE.MANAGER_SCREEN_CONTROLLER.onResize(this.adjustFirstPartCamera);
        //cc.view.setResizeCallback(this.partsDirtyPos);


        //sort parts as per their difficulty
        // for(var x=0; x<this.parts.length; x++){
        //     var score = partsDifficultyScore[this.parts[x].name].difficultyScore;
        //     if(score == undefined) score = Math.floor(Math.random() * 10)+1;// random 1-10 difficulty if not avaliable
        //     this.parts[x].difficultyScore = score;
        // }
        // this.parts.sort(function(a, b){
        //     return a.difficultyScore-b.difficultyScore; //low - high difficulty sorted
        // })




    },
    // partsDirtyPos(){
    //     if(that.part_1 != null) that.part_1.getComponent('part_manager').dirtyPos = true;
    //     if(that.part_2 != null) that.part_2.getComponent('part_manager').dirtyPos = true;
    // },
    pausePartsAnimation(){
        var recurse = function(node){
            for(var x=0; x<node.children.length; x++) recurse(node.children[x]);
            var anim = node.getComponent(cc.Animation);
            if(anim != null) anim.pause();
        }
        if(this.part_1 != null) recurse(this.part_1);
        if(this.part_2 != null) recurse(this.part_2);
    },
    resumePartsAnimation(){
        var recurse = function(node){
            for(var x=0; x<node.children.length; x++) recurse(node.children[x]);
            var anim = node.getComponent(cc.Animation);
            if(anim != null) anim.resume();
        }
        if(this.part_1 != null) recurse(this.part_1);
        if(this.part_2 != null) recurse(this.part_2);
    },
    onOutfitChange(evt){
        var data = evt  .getUserData();
        this.currentBodyFrame = data.bodyFrame;
        this.currentHandsFrame = data.handsFrame;
        if(this.part_1 != null) this.part_1.getComponent('part_manager').setOutfit(this.currentBodyFrame, this.currentHandsFrame);
        if(this.part_2 != null) this.part_2.getComponent('part_manager').setOutfit(this.currentBodyFrame, this.currentHandsFrame);
    },
    trashAllParts(){
        while(this.partsSpawnArea.children[0] != undefined){
            var part =  this.partsSpawnArea.children[0];
            if(part.name != 'finish_line') part.getComponent('part_manager').removeListners();
            this.partsSpawnArea.removeChild(part);
        } 
        this.part_1 = null;
        this.part_2 = null;
    },
    trashPart(partRef){
        partRef.removeListners();
        this.partsSpawnArea.removeChild(partRef);
        
        this.part_1 = null;
        this.part_2 = null;
    },
    reset(){//usually called on game over hitting restart button
        console.log('reset called')
        this.trashAllParts();
        this.makePart({catched:1, powerup:0, star:0});
        this.makePart();
    },
    resetBoosters(){
        if(this.part_2 != null) this.part_2.getComponent('part_manager').resetBoosters();
    },
    startOver(){//usually called on starting fresh game 
        this.trashAllParts();
        this.makePart({catched:1, powerup:0, star:0});
        console.log("startOver called")
    },
    loadTutorialPart(){//usually called on starting first time game
        this.trashAllParts();
        this.makePart({catched:1, powerup:0, star:0, highlighter:1});
        
    },
    removeHighliter(){
        this.part_2.getComponent('part_manager').removeHighlighter();
    },
    getBallSpawnPosition(){
        return this.part_1.getComponent('part_manager').getBallAbsSpawnPostion();
    },
    resetAdjustment(){
        this._partIndex = 0;
        this._yDev = 0;
        this._xDev = 0;
        this._powerup = 0;//70% chance that powerup will appear
        this._star = 0.7;//80% chance that star will appear
    },
    adjustPartIndexDifficulty(){
        this._partIndex++; //_partIndex acts as window in this.parts array, it will become higher with every call
        if(this._partIndex>this.parts.length-1) this._partIndex = this.parts.length-1;
    },
    adjustYDev(){
        this._yDev += 10;
        if(this._yDev>100) this._yDev = 100;
    },
    adjustXDev(){
        this._xDev += 5;
        if(this._xDev>100) this._xDev = 100;
    },
    adjustPowerup(){
        this._powerup += 0.05;
        if(this._powerup>=0.3){
            this._powerup = 0;
        } 
    },
    adjustStar(){
        this._star -= 0.05;
        if(this._star<0.5) this._star = 0.8;
    },

    _getPartIndex(){
        if(this.part_2 == null) return 0;

        var notAllowedParts = partsDefination[this.part_2.name].notAllowedParts;
        var partsIndexList = [];
        for (var x=0; x<=this._partIndex; x++){
            var partName = parseInt(this.parts[x].name.replace("part", ""));
            if(notAllowedParts.indexOf(partName) == -1){
                partsIndexList[partsIndexList.length] = x;
            }
        }
        var i = Math.floor(Math.random() * partsIndexList.length);
        return partsIndexList[i]  == undefined?0:partsIndexList[i]; //undefined when all parts in range excluded to include
    },
    _getPartLevel(){
        return 0;
    },
    _getStar(){
        var point = (Math.floor(Math.random() * 100)+1)/100;
        if(point<=this._star) return 1;
        return 0;
    },
    _getPowerup(){
        var point = (Math.floor(Math.random() * 100)+1)/100;

        if(point<=this._powerup){
            //create powerup
            var p = Math.floor(Math.random() * 2)+1; //1=projectile, 2=magnet
            return p;
        }
        return 0;
    },
    _getHighlighter(){
        return 0;
    },
    _getCatched(){
        return 0;
    },
    _getXDis(){
        return Math.floor(Math.random() * this._xDev)+1;
    },
    _getYDis(){
        var y = Math.floor(Math.random() * this._yDev) + 1;
        var alter = Math.floor(Math.random() * 2);
        alter = alter == 0?-1:1;
        y *= alter;
        return y;
    },
    getPartData(customData){ //when some properties missing then it will get properties based on difficulty curve
        customData = customData == undefined?{}:customData;
        //template
        var data = {
            index:0,   //part name
            level:0,   //0=easy, 1=medium, 2=hard,
            star:0,    //0=no, 1=yes     
            powerup:0, //0=none, 1=projectile, 2=magnet 
            highlighter:1, //0=disable, 1=enable
            catched: 0, //0=no, 1=yes
            xDis:0, //horizontal distance between parts
            yDis:0, //vertical distance between parts
        }

        //index
        if(customData.index == undefined) data.index = this._getPartIndex();
        else data.index = customData.index;
        //star
        if(customData.star == undefined) data.star = this._getStar();
        else data.star = customData.star;
        //powerup
        if(customData.powerup == undefined) data.powerup = this._getPowerup();
        else data.powerup = customData.powerup;
        //highlighter
        if(customData.highlighter == undefined) data.highlighter = this._getHighlighter();
        else data.highlighter = customData.highlighter;
        //catched
        if(customData.catched == undefined) data.catched = this._getCatched();
        else data.catched = customData.catched;
        //xDis
        if(customData.xDis == undefined) data.xDis = this._getXDis();
        else data.xDis = customData.xDis;
        //yDis
        if(customData.yDis == undefined) data.yDis = this._getYDis();
        else data.yDis = customData.yDis;

        return data;

    },
    makeFinishLine(){
        this.makePart({index:0,level:0,star:0,powerup:0,highlighter:0,catched: 0,xDis:0,yDis:0});
        var finishLineNode = cc.instantiate(this.finishLine);
        finishLineNode.x = this.part_2.x;
        finishLineNode.y = this.part_2.y;
        this.partsSpawnArea.removeChild(this.part_2);
        this.partsSpawnArea.addChild(finishLineNode);
    },
    disableParts(){
        if(this.part_1 != null) this.part_1.active = false;
        if(this.part_2 != null) this.part_2.active = false;
    },
    enableParts(){
        if(this.part_1 != null) this.part_1.active = true;
        if(this.part_2 != null) this.part_2.active = true;
    },
    adjustFirstPartCamera(screen){
        if(self.part_2 != null) return;
        if(screen == undefined) screen = cc.view.getFrameSize();

        var margin = 20;
        var zoom = 1;
        if(screen.width/screen.height >1.9){
            margin = 60;
            zoom = 0.8
        } 

        var data = {
            position: cc.v2(0, margin),
            zoom: zoom
        }
        CONST_SCENE.CAMERA_COMPONENT.setCamera(data);
        
    },
    makePart(pData){
        
        
        pData = this.getPartData(pData);
       
        //PART
        var fab = this.parts[pData.index];
        var part = cc.instantiate(fab);
        var part_manager = part.getComponent('part_manager');
        var aabb = part.getChildByName('aabb');

        part.name = fab.name;
        aabb.opacity = 0;

       

    
        //Adjust part_1 & part_2 poniters
        if(this.part_1 == null) this.part_1 = part;
        else if(this.part_2 == null) this.part_2 = part;
        else{
            this.part_1 = this.part_2;
            this.part_2 = part;
        }
        //Place part
        if(this.part_2 == null){//means there is only 1 part created
            this.part_1.x = 0;
            this.part_1.y = 0;
        }
        else{
            var aabb1 = this.part_1.getChildByName('aabb');
            var aabb2 = this.part_2.getChildByName('aabb');
            //horizontal alignment with both aabb boxes distance
            this.part_2.x = (this.part_1.x+aabb1.x)+(aabb1.width/2)+(aabb2.width/2);
            this.part_2.y = this.part_1.y;
            //this.part_2.x = (this.part_1.x+aabb1.x)+(aabb1.width/2)
            
        }

        // if(part.name == "part10"){
        //     pData.xDis = 0;
        //     pData.yDis = 0;
        //     console.log("adjusted");
        // }

        //xDis
        part.x = (part.x + pData.xDis);
        //yDis
        part.y = (part.y + pData.yDis);


        //check if newly place part is out of range of projectile
            var heightAdjustment = 0;
            if(this.part_2 != null){
                var player = this.part_1.getChildByName('player');
                var hands = player.getChildByName('hands');

                var sX = this.part_1.x+player.x+hands.x;
                var sY = this.part_1.y+player.y+hands.y;
                //console.log(sX, sY, "hands abs. pos");

                var aabb = this.part_2.getChildByName('aabb');
                var eX = this.part_2.x-(aabb.width/2);
                var eY = this.part_2.y+(aabb.height/2);

                //console.log(eX, eY, "aabb end");
                var maxThrowVelocity = 1495;
                var angleToCrossObstacle = Math.atan2(eY-sY, eX-sX)*(180/Math.PI);
                var maxHeightCanCover = this.ballNode.getComponent('ballController').getMaxHeight(maxThrowVelocity, angleToCrossObstacle);
                var obstacleHeight = eY-sY;

                if(obstacleHeight>maxHeightCanCover) heightAdjustment = obstacleHeight-maxHeightCanCover;

                //console.log("height can cover", maxHeight, "obstacle height", eY-sY, "angle", angleToCrossObstacle, "<<");
            }
            console.log('heightAdjustment', heightAdjustment);
            part.y -= heightAdjustment;

  
        //now add part to scene, add this at end because other wise due to static body it will not move
        this.partsSpawnArea.addChild(part);
        part_manager.setOutfit(this.currentBodyFrame, this.currentHandsFrame); //call at this point because part onload been called and the player will be added
        
        //following settings depends on player Node so it will be avaliable only after adding to scene
        //star
        if(pData.star == 1) part_manager.placeStar();
        //powerup
        if(pData.powerup == 1) part_manager.placeProjectilePowerup();
        else if(pData.powerup == 2) part_manager.placeMagnetPowerup();
        //highlighter
        if(pData.highlighter == 1) part_manager.placeHighlighter();
        //catched
        if(pData.catched == 1) part_manager.setCatchState(true);
        else part_manager.setCatchState(false);


        //Adusting zoom
        if(this.part_1 != null && this.part_2 == null){//there is only one part
            this.adjustFirstPartCamera();
        }
        //Adjusting the camera position and zoom
        if(this.part_1 != null && this.part_2 != null){
            //var evt = new cc.Event.EventCustom(CONST_EVENT.SET_CAMERA, true);
            var frameMargin = 0;
            var aabbNode_l = this.part_1.getChildByName('aabb');
            var aabbNode_r = this.part_2.getChildByName('aabb');

            var left_x = this.part_1.x + aabbNode_l.x;
            var left_y = this.part_1.y + aabbNode_l.y;
            var right_x = this.part_2.x + aabbNode_r.x;
            var right_y = this.part_2.y + aabbNode_r.y;

            var left_width = aabbNode_l.width;
            var left_height = aabbNode_l.height;
            var right_width = aabbNode_r.width;
            var right_height = aabbNode_r.height;

            //finding top_left_x
            var node_left_top_left_x = left_x-(left_width/2);
            var node_right_top_left_x = right_x-(right_width/2);
            var top_left_x = node_left_top_left_x<node_right_top_left_x?node_left_top_left_x:node_right_top_left_x;
            //finding top_left_y
            var node_left_top_left_y = left_y+(left_height/2);
            var node_right_top_left_y = right_y+(right_height/2);
            var top_left_y = node_left_top_left_y>node_right_top_left_y?node_left_top_left_y:node_right_top_left_y;
            
            //finding bottom_right_x
            var node_left_bottom_right_x = left_x+(left_width/2);
            var node_right_bottom_right_x = right_x+(right_width/2);
            var bottom_right_x = node_left_bottom_right_x>node_right_bottom_right_x?node_left_bottom_right_x:node_right_bottom_right_x;
            //finding bottom_right_y
            var node_left_bottom_right_y = left_y-(left_height/2);
            var node_right_bottom_right_y = right_y-(right_height/2);
            var bottom_right_y = node_left_bottom_right_y<node_right_bottom_right_y?node_left_bottom_right_y:node_right_bottom_right_y;


            

            //setting red dots for camera frame
            if(window.p1 != undefined) CONST_SCENE.CANVAS_NODE.removeChild(p1);
            if(window.p2 != undefined) CONST_SCENE.CANVAS_NODE.removeChild(p2);
            if(window.p3 != undefined) CONST_SCENE.CANVAS_NODE.removeChild(p3);
            window.p1 = cc.instantiate(this.point);
            window.p2 = cc.instantiate(this.point);
            window.p3 = cc.instantiate(this.point);
            p1.opacity = p2.opacity = p3.opacity = 0;
            CONST_SCENE.CANVAS_NODE.addChild(p1);
            CONST_SCENE.CANVAS_NODE.addChild(p2);
            CONST_SCENE.CANVAS_NODE.addChild(p3);


            var topLeft = cc.v2(top_left_x, top_left_y);
            var bottomRight = cc.v2(bottom_right_x, bottom_right_y);

            //adding margins
            topLeft.x -= frameMargin;
            topLeft.y += frameMargin;
            bottomRight.x += frameMargin;
            bottomRight.y -= frameMargin;


            var width = bottomRight.x-topLeft.x;
            var height = topLeft.y-bottomRight.y;
            var designResolution = cc.director.getScene().getChildByName('Canvas').getComponent(cc.Canvas).designResolution;
            var visibleRectangle = cc.view.getVisibleSize();
            var widthScale =  visibleRectangle.width/width;
            var heightScale = visibleRectangle.height/height;
            var finalScale = widthScale<heightScale?widthScale:heightScale;
            var center = cc.v2(topLeft.x+((width)/2), bottomRight.y+((height)/2))
            var data = {
                position: cc.v2(center.x, center.y),
                zoom: finalScale,
                width: width,
                height: height
            }

            //adjusting frame pointers
            p1.x = topLeft.x;
            p1.y = topLeft.y;
            p2.x = bottomRight.x;
            p2.y = bottomRight.y;
            p3.x = center.x;
            p3.y = center.y;
            
            CONST_SCENE.CAMERA_COMPONENT.setCamera(data);
            CONST_SCENE.DEATH_BORDER_COMPONENT.setDeathBorder(data);
            
        }
        


        


      
       
  
    },
    deletePart(callback){
        var self = this;
        var tweenObj = {
            from: {scale:this.part_1.scale, opacity: this.part_1.opacity},
            to: {scale: 0, opacity: 0},
            duration: 300,
            easing:'easeOutQuad',
            step: function(state){
                //self.part_1.scale = state.scale;
                self.part_1.opacity = state.opacity;
            }
        }

        shifty.tween(tweenObj).then(function(){
            self.part_1.getComponent('part_manager').removeListners();
            self.partsSpawnArea.removeChild(self.part_1);
            callback();
        });


    },
    
});
