var shifty = require("shifty");
var CONST_EVENT = require('event');
cc.Class({
    extends: cc.Component,

    properties: {
        verticalLength: 100,
        time: 1,
        ground:cc.Node,
        easingFunctionName: 'easeInQuint'
       
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {

      
        this.stopMovement = false;
       this.tween1 = null;
       this.tween2 = null;
        cc.systemEvent.on(CONST_EVENT.BALL_FIRST_TIME_CATCHED, function(){
            if(self.tween1 != null) self.tween1.pause();
            if(self.tween2 != null) self.tween2.pause();
        }, this);

      
        var self = this
        var ground = this.ground;
        var groundPos = cc.v2(ground.x, ground.y);
        var aboveToBelow = {
            from: {x:this.node.x, y:this.node.y},
            to: {x:this.node.x, y:this.node.y-this.verticalLength},
            duration: this.time*1000,
            easing:this.easingFunctionName,
            step: function(state){
                self.node.x = state.x;
                self.node.y = state.y;
                ground.x = groundPos.x;
                ground.y = groundPos.y;
            }
        }
        var belowToAbove = {
            from: {x:this.node.x, y:this.node.y-this.verticalLength},
            to: {x:this.node.x, y:this.node.y},
            duration: this.time*1000,
            easing:this.easingFunctionName,
            step: function(state){
                self.node.x = state.x;
                self.node.y = state.y;
                ground.x = groundPos.x;
                ground.y = groundPos.y;
            }
        }

        var sequence = function(){
            self.tween1 = new shifty.Tweenable();
            self.tween1.setConfig(aboveToBelow);
            self.tween1.tween().then(function(){
                self.tween2 = new shifty.Tweenable();
                self.tween2.setConfig(belowToAbove);
                self.tween2.tween().then(sequence);
            })   
        }

        sequence(); //start movement

    }
});
