var shifty = require("shifty");
//var CONST_EVENT = require('const/event');
var CONST_SCENE = require('scene');

cc.Class({
    extends: cc.Component,

    properties: {
        cam_mid_up:cc.Node,
        cam_mid_down:cc.Node
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
       // cc.systemEvent.on(CONST_EVENT.SET_CAMERA, this.onSetCamera, this);
       window.camController = this;
       this.customizationMargin = 200;
       this.customizationTime = 400;
    },

    customizationSceneOn(){
        var self = this;
        var tweenObj = {
            from: {x:this.node.x},
            to: {x: this.node.x - this.customizationMargin},
            duration: this.customizationTime,
            easing:'easeOutQuad',
            step: function(state){
               self.cam_mid_down.x = self.cam_mid_up.x = self.node.x = state.x;
            }
        }

        shifty.tween(tweenObj)


    },
    customizationSceneOff(){
        var self = this;

        var tweenObj = {
            from: {x:this.node.x},
            to: {x: this.node.x + this.customizationMargin},
            duration: this.customizationTime,
            easing:'easeOutQuad',
            step: function(state){
               self.cam_mid_down.x = self.cam_mid_up.x = self.node.x = state.x;
            }
        }

        shifty.tween(tweenObj)


    },
    setCamera(data){

        // var topLeft = event.data.topLeft;
        // var bottomRight = event.data.bottomRight;
        // var center = event.data.center;
        // var width = event.data.width;
        // var height = event.data.height;
        // var canvasWidth = 800;
        // var canvasHeight = 480;

       //var widthScale = canvasWidth/width;
       //var heightScale = canvasHeight/height;


       var newPosition = data.position;
       var newZoom = data.zoom;
       var self = this;
       // widthScale = heightScale;
      // widthScale = widthScale == Infinity?0:widthScale;
      // heightScale = heightScale == Infinity?0:heightScale;

      // var smallerScale = widthScale<heightScale?widthScale:heightScale;

       //this.node.x = centerX;
       //this.node.y = centerY;
    
         window.playingCamera = CONST_SCENE.CAMERA_NODE.getComponent(cc.Camera);
        // var absX = 334/newZoom;
        // var absY = 201.2/newZoom;
        // window.vstar = ps.node.getChildByName('vstar');
        // vstar.x = (newPosition.x + absX);
        // vstar.y = (newPosition.y + absY);

        //now convert the value as if it's in zoom


        var tweenObj = {
            from: {x:this.node.x, y:this.node.y, zoom:CONST_SCENE.CAMERA_NODE.getComponent(cc.Camera).zoomRatio},
            to: {x:newPosition.x, y:newPosition.y, zoom:newZoom},
            duration: 600,
            easing:'easeOutQuad',
            step: function(state){
               self.cam_mid_down.x = self.cam_mid_up.x = self.node.x = state.x;
               self.cam_mid_down.y = self.cam_mid_up.y = self.node.y = state.y;
               self.cam_mid_down.getComponent(cc.Camera).zoomRatio = self.cam_mid_up.getComponent(cc.Camera).zoomRatio = self.node.getComponent(cc.Camera).zoomRatio = state.zoom;
            }
        }

        shifty.tween(tweenObj)
          

    },

    start () {

    },

});
