var CONST_EVENT = require('event');
var shifty = require("shifty");
cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        cc.systemEvent.on(CONST_EVENT.BALL_FIRST_TIME_CATCHED, this.removeGround, this);
    },
    removeGround(evt){
        var handsNode = evt.getUserData();
       // if(handsNode.partNodeRef.uuid == this.node.uuid){//this hand node belongs to this part so take action on first catching the ball

            var that = this;
            var tweenObj = {
                from: {opacity:255},
                to: {opacity:0},
                duration: 300,
                easing:'linear',
                step: function(state){
                    that.node.opacity = state.opacity;
                }
            }
            
            //first immediatly remove collider
            that.node.removeComponent(cc.RigidBody);
            
            shifty.tween(tweenObj).then(function(){
                that.node.destroy();
            })            
            
        //}
    },
});
